* {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Hind Madurai", sans-serif;
  background-color: #ffffff;
}

a:visited,
a:link {
  color: currentColor;
  text-decoration: none;
}

a:link:active,
a:visited:active {
  color: currentColor;
}

::-moz-selection {
  background-color: #d7492b;
  color: #ffffff;
}

::selection {
  background-color: #d7492b;
  color: #ffffff;
}

/*

Title: Collabofide
Author: J Dutra M Nt
Author URL: https://dutraneto.com/portfolio
Starts on: 03/08/20

*/
.header {
  width: 100%;
  height: 100vh;
  background: #ffffff url("../../assets/img/hero-full.svg");
  background-size: contain;
  background-position: top right;
  background-repeat: no-repeat;
}

.container {
  padding: calc(9vh) 8vw;
  max-width: 1920px;
  width: 100%;
  height: 100%;
}

.container a {
  align-self: flex-start;
}

.container .logo {
  min-width: 150px;
  width: 12vw;
  height: auto;
}

.container__wrapper {
  height: 100%;
  max-width: 640px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.container__wrapper .heading {
  margin-bottom: 3.5rem;
}

.container__wrapper .heading h1 {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 3rem;
  color: #d7492b;
  font-weight: 700;
  line-height: 59px;
}

.container__wrapper .heading h3 {
  width: 400px;
  font-family: "Lora", serif;
  font-size: 1.5rem;
  font-style: italic;
  font-weight: normal;
  line-height: 31px;
  color: #343c4c;
}

.container__wrapper .cta__subscribe {
  position: relative;
  width: 400px;
}

.container__wrapper .cta__subscribe p {
  font-family: "Hind Madurai", sans-serif;
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.5rem;
  color: #343c4c;
  margin-bottom: 10px;
}

.container__wrapper .cta__subscribe-control {
  width: 100%;
  height: 3rem;
  border: 1px solid #d7492b;
  border-radius: 30px;
  padding-left: 10px;
  font-size: 14px;
  background-color: transparent;
  color: #d7492b;
}

.container__wrapper .cta__subscribe-btn {
  position: absolute;
  height: 3rem;
  width: 10rem;
  border: 0;
  background-color: #d7492b;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  text-transform: uppercase;
  color: #ffffff;
  bottom: 0;
  right: 0;
  cursor: pointer;
  transition: background-color 0.4s ease-in-out;
  font-size: 14px;
  font-weight: 300;
}

.container__wrapper .cta__subscribe-btn:hover {
  background-color: #c44125;
}

.container__wrapper .cta__illustration {
  display: none;
  width: 100%;
  text-align: center;
}

@media only screen and (max-width: 1100px) {
  .header {
    background-image: none;
    height: auto;
  }
  .container {
    padding: 3rem;
  }
  .container .logo {
    min-width: 120px;
    margin-bottom: 5rem;
  }
  .container__wrapper {
    max-width: 80%;
    align-items: center;
    margin: auto;
  }
  .container__wrapper .heading {
    margin-bottom: 3rem;
  }
  .container__wrapper .heading h1 {
    text-align: center;
    font-size: 2.5rem;
  }
  .container__wrapper .heading h3 {
    font-size: 20px;
    width: 100%;
  }
  .container__wrapper .cta__subscribe {
    width: 70%;
    margin: 0 auto 4rem auto;
  }
  .container__wrapper .cta__subscribe p {
    font-size: 14px;
  }
  .container__wrapper .cta__subscribe-control {
    height: 2.5rem;
    font-size: 16px;
  }
  .container__wrapper .cta__subscribe-btn {
    height: 2.5rem;
    font-size: 12px;
  }
  .container__wrapper .cta__illustration {
    display: block;
  }
}

@media only screen and (max-width: 768px) {
  .logo {
    min-width: 130px;
  }
  .container__wrapper {
    max-width: 100%;
    justify-content: flex-start;
    align-items: center;
  }
  .container__wrapper .heading {
    margin-bottom: 4.5rem;
    text-align: center;
  }
  .container__wrapper .heading h3 {
    width: 100%;
  }
  .container__wrapper .cta__subscribe {
    width: 80%;
  }
  .container__wrapper .cta__subscribe p {
    font-size: 16px;
    margin-bottom: 20px;
  }
  .container__wrapper .cta__subscribe-control {
    height: 3rem;
  }
  .container__wrapper .cta__subscribe-btn {
    height: 3rem;
    font-size: 12px;
  }
}

@media only screen and (max-width: 576px) {
  .container {
    padding: 3rem 1rem;
  }
  .container__wrapper .heading h1 {
    font-size: 2rem;
  }
  .container__wrapper .cta__subscribe {
    margin-bottom: 4rem;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
  }
  .container__wrapper .cta__subscribe-btn {
    width: auto;
    padding: 0 1rem;
  }
  .container__wrapper .cta__illustration {
    width: 100%;
  }
  .container__wrapper .cta__illustration img {
    width: 90%;
    height: auto;
  }
}

@media screen and (min-width: 120em) {
  .container__wrapper .heading h1 {
    font-size: 4rem;
    margin-bottom: 1rem;
  }
  .container__wrapper .heading h3 {
    width: 600px;
    font-size: 2rem;
  }
  .container__wrapper .cta__subscribe {
    width: 500px;
  }
}

@media only screen and (min-width: 1921px) {
  .header {
    background-size: cover;
  }
}

